.appeal-section {
    --section-gap-y: 12rem;
    --section-gap-y-: -12rem;
    background: $color-accent-1;
    min-height: 100vh;
    padding: var(--section-gap-y) 0;
    display: flex;
    color: $color-white-1;
    border-bottom: 1px solid $color-accent-1;
    position: relative;
    overflow: hidden;
    z-index: 0;
    &__container {
        display: flex;
    }
    &__content {
        display: flex;
        flex-direction: column;
    }
    &__title {
        width: 100%;
        max-width: 67%;
        margin: 0 auto 8rem;
        line-height: 1.065;
        text-align: center;
        transition: all .8s;
        transition-property: color;
        order: -1;
        span {
            color: $color-gray-2;
            transition: all .8s;
            transition-property: color;
        }
    }
    &__btn-link:hover ~ &__title,
    &__btn-link:hover ~ &__title span {
        color: $color-accent-1;
    }
    &__btn-link {
        margin: auto 0 var(--section-gap-y-);
        padding: 10rem 0 9rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        font-weight: 800;
        font-size: var(--fs-4);
        color: $color-accent-1;
        text-transform: uppercase;
        position: relative;
        &:hover {
            text-decoration: none;
            @media (min-width: $lg-mf) {
            }
        }
        &-bg {
            width: 64.6rem;
            height: 64.6rem;
            border-radius: 50%;
            position: absolute;
            top: 0;
            left: 50%;
            transform: translate(-50%, 0);
            z-index: -1;
            &:before,
            &:after {
                content: '';
                border-radius: inherit;
                position: absolute;
            }
            &:before {
                background: $color-white-2;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                transition: all .38s;
                z-index: -2;
            }
            &:after {
                border: 1px solid $color-accent-1;
                top: -1px;
                right: -1px;
                bottom: -1px;
                left: -1px;
                z-index: -1;
            }
        }
        &:hover &-bg:before {
            transform: scale(5);
        }
        &:hover &-bg:after {
        }
        .icon {
            width: 4.4rem;
            height: 4.4rem;
            margin: 0 auto 2rem;
            display: block;
        }
    }
}