#root {
    position: relative;
    overflow: clip;
}

.container {
    width: 100%;
    max-width: 144rem;
    margin: 0 auto;
    padding: 0 var(--gap-fluid);
}

svg,
.u-icon {
    width: 1em;
    height: 1em;
    display: inline-block;
    color: inherit;
    fill: currentColor;
}

.img-cover {
    display: block;
    position: relative;
    overflow: hidden;
    img {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        object-fit: cover;
    }
}

.img-contain {
    display: block;
    position: relative;
    overflow: hidden;
    img {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        object-fit: contain;
    }
}

.area-click {
    --indent: -.5rem;
    position: relative;
    &:before {
        content: '';
        position: absolute;
        top: var(--indent);
        right: var(--indent);
        bottom: var(--indent);
        left: var(--indent);
    }
}

.link-cover {
    &:before {
        width: 100%;
        height: 100%;
        content: '';
        position: absolute;
        top: 0;
        left: 0;
    }
}

.btn-reset {
    background: transparent;
    margin: 0;
    padding: 0;
    font-size: 0;
    outline: none;
    border: none;
    border-radius: 0;
}

.visually-hidden,
.visually-hidden-focusable:not(:focus):not(:focus-within) {
    width: 1px!important;
    height: 1px!important;
    margin: -1px!important;
    padding: 0!important;
    position: absolute!important;
    border: 0!important;
    clip: rect(0,0,0,0)!important;
    white-space: nowrap!important;
    overflow: hidden!important;
}

.font-family-accent {
    font-family: $ff-2;
}

.content {
    line-height: 1.4;
    > :first-child {
        margin-top: 0;
    }
    > :last-child {
        margin-bottom: 0;
    }
    p {
        margin: 0 0 1em;
    }
}

.btn {
    max-width: 100%;
    min-height: var(--btn-height);
    padding: var(--btn-padding-y) var(--btn-padding-x);
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-style: normal;
    font-weight: 800;
    font-size: inherit;
    font-size: var(--btn-font-size);
    font-family: $ff-1;
    line-height: 1.2;
    text-align: center;
    text-transform: uppercase;
    border: none;
    border-radius: 0;
    outline: none;
    user-select: none;
    transition: all .24s;
    position: relative;
    cursor: pointer;
    overflow: hidden;
    z-index: 0;
    &:hover {
        text-decoration: none;
    }
    &.disabled,
    &:disabled {
        background: #ccc;
        color: #000;
        opacity: .6;
        pointer-events: none;
    }
    &--theme {
        &-accent-outline {
            background: transparent;
            color: $color-accent-3;
            border: 1px solid $color-accent-1;
            border-radius: 1rem;
            &:hover {
                @media (min-width: $lg-mf) {
                }
            }
            &:focus,
            &:active {
            }
        }
        &-accent-fill {
            background: $color-accent-1;
            color: $color-white-1;
            border-radius: 1rem;
            &:hover {
                @media (min-width: $lg-mf) {
                }
            }
            &:focus,
            &:active {
            }
        }
    }
    &--size {
        &-sm {
            --btn-height: 4.8rem;
            --btn-padding-x: 5.4rem;
            --btn-padding-y: 1.2rem;
            --btn-font-size: var(--fs-7);
        }
        &-md {
            --btn-height: 5.6rem;
            --btn-padding-x: 3.8rem;
            --btn-padding-y: 1.7rem;
            --btn-font-size: var(--fs-7);
        }
    }
    &--width {
        &-100 {
            width: 100%;
        }
    }
    &__inner {
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        z-index: 1;
    }
    &__title {
    }
}

.logo {
    @extend .area-click;
    display: inline-block;
    font-weight: normal;
    font-size: var(--fs-5);
    font-family: $ff-2;
    text-transform: uppercase;
    white-space: nowrap;
    transition: all .34s;
    transition-property: opacity;
    cursor: pointer;
    &:hover {
        text-decoration: none;
        @media (min-width: $lg-mf) {
            opacity: .6;
        }
    }
}

.services-card {
    background: $color-white-1;
    padding: 7.2rem 3.6rem;
    border-radius: 2rem;
    &__inner {
    }
    &__counter {
        margin: 0 0 2rem;
        font-weight: 800;
        font-size: 17rem;
        line-height: 1;
        &:after {
            content: counter(services-card-counter);
            font: inherit;
        }
    }
    &__title {
        min-height: 3.12em;
        margin: 0 0 5.9rem;
        line-height: 1.04;
        text-transform: uppercase;
    }
    &__description {
        margin: 0 2.6rem 0 0;
        font-weight: 500;
        font-size: var(--fs-5);
        color: rgba($color-accent-1, .7);
    }
}

.sc-block {
    &__list {
        margin: -.7rem;
        padding: 0;
        display: flex;
        list-style-type: none;
    }
    &__item {
        margin: .7rem;
        padding: 0;
    }
    &__link {
        background: $color-white-1;
        width: 4rem;
        height: 4rem;
        display: flex;
        justify-content: center;
        align-items: center;
        color: $color-accent-1;
        border-radius: 50%;
        transition: all .4s;
        &:hover {
            text-decoration: none;
            @media (min-width: $lg-mf) {
                background: $color-accent-1;
                color: $color-white-1;
            }
        }
    }
    &__icon {
        width: 2rem;
        height: 2rem;
        display: block;
    }
}