.intro-section {
    position: relative;
    overflow: hidden;
    &__container {
        min-height: 100vh;
        padding-top: 10.4rem;
        padding-bottom: 10.4rem;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column;
    }
    &__content {
        max-width: 88rem;
        margin: 0 auto;
        text-align: center;
    }
    &__title {
        margin: 0 0 4.5rem;
    }
    &__btn {
        margin: 0 0 5rem;
    }
    &__scroll-link {
        margin: auto 0 -10.4rem;
        padding: 15.6rem 0 10rem;
        display: flex;
        justify-content: center;
        position: relative;
        z-index: 0;
        &:hover {
            text-decoration: none;
            @media (min-width: $lg-mf) {
            }
        }
        &:before {
            background: $color-white-2;
            width: 84rem;
            height: 84rem;
            content: '';
            border-radius: 50%;
            position: absolute;
            top: 0;
            left: 50%;
            transform: translate(-50%, 0);
            z-index: -1;
        }
        .icon {
            width: 6.2rem;
            height: 6.2rem;
            display: block;
        }
    }
}