.services-section {
    background: $color-accent-1;
    min-height: 100vh;
    padding: 6rem 0;
    display: flex;
    align-items: center;
    &__container {
    }
    &__title {
        margin: 0 0 4.5rem;
        span {
            color: $color-gray-1;
        }
    }
    &__list {
        margin: 0 var(--gap-);
        display: flex;
        counter-reset: services-card-counter;
        &-col {
            max-width: 33.33333%;
            padding: 0 var(--gap);
            flex: 0 0 33.33333%;
            counter-increment: services-card-counter;
        }
        &-card {
            height: 100%;
        }
    }
}