.footer {
    background: $color-white-2;
    padding: 5.6rem 0;
    &__container {
    }
    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__logo {
        flex-shrink: 0;
    }
    &__sc-block {
    }
    &__copyright {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        color: rgba($color-accent-1, .5);
        &-description {
            max-width: 111rem;
            margin: 0 var(--gap-fluid) 0 0;
        }
        &-author {
            flex-shrink: 0;
            text-align: right;
        }
    }
    &__divider {
        background: rgba($color-accent-1, .2);
        width: 100%;
        height: 1px;
        margin: 2.6rem 0;
        display: block;
        border: none;
        outline: none;
    }
}

.asfafsf {
    height: 100vh;
    position: relative;
    z-index: 11;
}